import * as React from 'react';
import { BrowserRouter, Route, StaticRouter, Switch } from 'react-router-dom';
import { hot } from 'react-hot-loader';
import MovieDetails from '../movie-details/movie-details';
import SearchMoviesPage from '../search-movies-page/search-movies-page';
import { CustomAlert } from '../custom-alert/custom-alert';
import { CustomLoader } from '../custom-loader/custom-loader';
import { ErrorPage } from '../error-page/error-page';

const App: React.FunctionComponent<any> = ({ Router, location, context }) => 
  <>
    <CustomAlert />
    <CustomLoader />
    <Router location={location} context={context}>
      <Switch>
        <Route exact path="/">
          <SearchMoviesPage />
        </Route>
        <Route path="/search">
          <SearchMoviesPage />
        </Route>
        <Route path="/film/:id">
          <MovieDetails />
        </Route>
        <Route path="*">
          <ErrorPage />
        </Route>
      </Switch>
    </Router>
  </>;

export default hot(module)(App);
